<template>
    <div class="service_page">
        <div class="service_setup">
            <h4 class="heading mb_4">Add new Service</h4>
            <form class="form_data" @submit.prevent="createProfileSubmit">
                <div class="left">
                    <div class="mb_3">
                        <!-- <base-selects
                            label="Work Station"
                            :options="get_mobile"
                            v-model="mobile"
                        ></base-selects> -->
                        <label for="" style="margin-bottom:6px;display: block;">Mobile</label>
                         <autocomplete :items="get_mobile"
                            filterby="mobile"
                            @change="onChange"
                            title="Mobile Number"
                            @selected="customerSelected"></autocomplete>
                    </div>

                    <div class="mb_3">
                        <base-select
                            label="Work Station"
                            :options="data"
                            v-model="select_change"
                        ></base-select>
                    </div>

                    <div class="mb_3">
                        <base-select1
                         label="Category"
                         :options="categories"
                          v-model="category"
                        >
                        
                        </base-select1>
                    </div>

<!-- v-if="getBusinessShop" -->

                    <div class="mb_3"  v-if="getBusinessShop">
                        <base-select
                         label="Community"
                         :options="communities"
                          v-model="community"
                        >

                        </base-select>
                    </div>
                    <div class="mb_3">
                        <!-- <base-input
                            label="Google Location"
                        v-model="origin"></base-input> -->

                        <label for="">Location</label>

                        <input type="text" name="" style="height:40px; width:100%;" ref="origin" id="">
                    </div>

                     <div class="mb_3">
                        <label for="business_name">Business Name</label>
                        <input type="text" style="height:40px; width:100%;" v-model="business_name" id="business_name">
                     </div>

                      <div class="mb_3">
                        <label for="address"><Address></Address></label>
                        <input type="text" style="height:40px; width:100%;" v-model="address" id="address">
                     </div>



                    <div class="mb_3 d_flex">
                        <base-radiobox
                            divClass="d_flex_10"
                            label="Pay Now"
                            attributeName="true"
                        ></base-radiobox>
                        <base-radiobox
                            divClass="d_flex_10"
                            label="Trial"
                        ></base-radiobox>
                    </div>
                    <div class="mb_3">
                        <base-button
                            small="btn_sm w_100 boder_radius1"
                            type="submit"
                        >Submit</base-button>
                    </div>
                </div>
               
            </form>
        </div>
    </div>
</template>
<script>
import { ref, onMounted, watch, computed } from 'vue'

import axios from 'axios';

export default {
    setup() {
        const select_change = ref('');
        const category = ref('');
        const mobile = ref('');
        const getBusinessShop = ref(false);
        const community = ref('');
        const origin = ref('');
        const location = ref('');
        const lat = ref('');
        const lng = ref('');
        const business_name = ref('');
        const address = ref('');


        const get_mobile = ref(['']);
         const data = ref([]);
         const categories = ref([]);
         const communities = ref([]);
         console.log(mobile)



      const getmobiles = async () =>{
            try{
                const mobiles = await axios.get('/api/user/get_mobiles');
                console.log(mobiles.data);
                if(mobiles.data){
                    get_mobile.value = mobiles.data;
                }else{
                    throw Error('User Mobile Number not found');
                }

            }catch(err){

                console.log(err.message);
            }
        }

        const getworstations = async () =>{
            try{
                const datas = await axios.get('/api/user/work_station');
                console.log(datas);
                if(datas.data){
                    data.value = datas.data;
                }else{
                    throw Error('Workstation data not found');
                }
               
            }catch(err){

                console.log(err.message);
            }
        }

         const getcategories = async (id) =>{
            try{
                const category = await axios.get(`/api/user/get_categories/${id}`);
                console.log(category);
                if(category.data){
                    categories.value = category.data;
                }else{
                    throw Error('categories data not found');
                }

               
            }catch(err){

                console.log(err.message);
            }
        }


         const getBusinessType = async (id) =>{
            try{
                const getBusinessShopon = await axios.get(`/api/user/get_categories_shop/${id}`);
                    console.log(getBusinessShopon.data);
                if(getBusinessShopon.data){
                    getCommunities()
                }
                    getBusinessShop.value = getBusinessShopon.data;
              
            }catch(err){

                console.log(err.message);
            }
        }


         const getCommunities = async () =>{
            try{
                const getCommunity = await axios.get('/api/user/get_community');
                console.log(getCommunity.data)
                communities.value = getCommunity.data;
              
            }catch(err){

                console.log(err.message);
            }
        }





        watch( select_change,
            (currentValue, prevValue) => {
             console.log(currentValue);
             getcategories(currentValue);
            },

       );


         watch( category,
            (currentValue, prevValue) => {
             getBusinessType(currentValue);
            },

       );

      
        // console.log(getBusinessType);
        onMounted(()=>{
            getworstations();
            getmobiles();
            console.log(origin.value)

                const originAutocomplete =  new google.maps.places.Autocomplete(
            origin.value,
            {
                bounds: new google.maps.LatLngBounds(
                    new google.maps.LatLng(45.2425296, -75.6971931)
                )
            }
        );


         originAutocomplete.addListener("place_changed", () => {
        const place = originAutocomplete.getPlace();
        location.value = place.formatted_address;
        lat.value = place.geometry.location.lat();
        lng.value = place.geometry.location.lng();
        console.log(place);

       });


        });

        const  customerSelected = (customer) => {
            // console.log(`Customer Selected:\nid: ${customer.id}\nname: ${customer.mobile}`);
            mobile.value = customer.mobile
        }


    


        const createProfileSubmit = async ()=>{
            const form = {
                mobile: mobile.value,
                workstation: select_change.value,
                category: category.value,
                community: community.value,
                location:location.value,
                lat: lat.value,
                lng: lng.value,
                business_name: business_name.value,
                address: address.value

            };

            


            axios.post('/api/user/user_service_profile_create', form)
            .then(res =>{
                console.log('ok'+res);
            })
        }

        return {
            select_change,
            data,
            categories,
            category,
            get_mobile,
            getBusinessShop,
            communities,
            community,
            customerSelected,
            origin,
            createProfileSubmit,
            business_name,
            address
            
            
        }
    },

   
}
</script>




<style lang="scss" scoped>
.mb_2 { margin-bottom: 8px; }
.mb_3 { margin-bottom: 12px; }
.mb_4 { margin-bottom: 16px; }
.d_flex { display: flex; column-gap: 20px; }
.d_flex_10 { display: flex; column-gap: 10px; }
.w_100 { width: 100%; }
.service_page {
    .service_setup {
        .form_data {
            width: 100%;
            display: flex;
            column-gap: 20px;
            .left {
                width: 400px;
                margin: 0 auto;
            }
           
        }
    }
}


$phone-width-small: 360px;
$phone-width: 575px;
$tablet-width: 767px;
$desktop-width: 1023px;

@media (max-width: #{$desktop-width}) {
    .service_page {
        .service_setup {
            .form_data {
                .left {
                    width: 280px;
                }
               
            }
        }
    }
}
@media (max-width: #{$tablet-width}) {
    .service_page {
        .service_setup {
            .form_data {
                .left {
                    width: 240px;
                }
                
            }
        }
    }
}

@media (max-width: #{$phone-width}) {
    .service_page {
        .service_setup {
            .form_data {
                width: 100%;
                display: block;
                .left {
                    width: 100%;
                }
               
            }
        }
    }
}

@media (max-width: #{$phone-width-small}) {
    
}
</style>